import logo from './logo.svg';
import './App.css';
import Person from "./components/Person";
import React , { Component } from 'react';
import Validation from "./components/validation";

class App extends Component {
  state = {
    persons : [ 
      {id:'fasde',name:"max",age:27},
      {id:'asde',name:"manu",age:17},
      {id:'cgfrfd',name:"alex",age:21}
    ],
    showPersons: false,
    userInput: ""
  }
  togglePersonsHandler = () => {
    this.setState({
      showPersons: !this.state.showPersons
    })
  }
  nameChangeHandler = (event , id ) => {
    const personIndex = this.state.persons.findIndex(p => {
      return p.id === id;
    })
    const person = {
      ...this.state.persons[personIndex]
    }
    person.name = event.target.value;
    const persons = [...this.state.persons];
    persons[personIndex] = person;
    this.setState({persons : persons})
  }
  deletePersonHandler = (personIndex) => {
    const persons = [...this.state.persons];
    persons.splice(personIndex, 1);
    this.setState({
      persons: persons
    })
  }
  inpChangeHandler = (event) => {
    this.setState({userInput: event.target.value})
  }
  render(){
    const style = {
      backgroundColor: 'white',
      font: 'inherit',
      border: '1px solid blue',
      padding: '8px',
      cursor: 'pointer'
    } 
    let persons = null;
    if(this.state.showPersons){
      persons = (
        <div>
          {this.state.persons.map((persons , index) => {
            return <Person 
            name={persons.name} 
            age={persons.age}
            key={persons.id}
            Click={this.deletePersonHandler.bind(this, index)}
            Change={(event)=>this.nameChangeHandler(event, persons.id)}
            ></Person>
          })}
        </div>
      )
    }
    return (
      <div className="App">
        <header className="App-header">
          <img src={logo} className="App-logo" alt="logo" />
          <p>
            create react App
          </p>
          <button 
          style={style}
          onClick={this.togglePersonsHandler}>show persons</button>
          {persons}
          <hr/>
          <input type="text" onChange={this.inpChangeHandler} value={this.state.userInput}/>
          <p>{this.state.userInput}</p>
          <Validation inputLength={this.state.userInput.length}/>
          
          
          
          <a
            className="App-link"
            href="https://reactjs.org"
            target="_blank"
            rel="noopener noreferrer"
          >
            Learn react
          </a>
        </header>
      </div>
    );
  }
}

export default App;
